import { useEffect, useState } from "react";
import { ethers } from "ethers";
function Home(){

    let [registor,setRegistor] = useState([]);


      useEffect(()=>{
if(!window.ethereum){
alert('interact with crypto wallet')
}else{
  let leading = async()=>{
    let contractAddress = "0xf377894B9Fee15f56e5ad62E9e7e149255FE3581";
    let provider = new ethers.providers.Web3Provider(window.ethereum);
    let abi = [
      "function registor() view returns(address[])",
      "function getData(address party) view returns (string cid)",
      "function voteCounter(address _party) view returns(uint)"
    ]
    let contract = new ethers.Contract(contractAddress,abi,provider);
    let registors = await contract.registor();
   console.log(registors)
    for(let i = 0; i<registors.length;i++){
      let data = await contract.getData(registors[i]);
      let voteCount = await contract.voteCounter(registors[i])
      let url = 'https://cloudflare-ipfs.com/ipfs/'+data;
      let response = await fetch(url)
      let res = await response.json()
      let index = res.cid.indexOf('/');
      let splitName = res.cid.slice(index,url.length);
      let splitcid = res.cid.slice(0,index)
      setRegistor((prev)=>{
        return [...prev,{
          address: registors[i],
          name: res.name,
         img: 'https://'+splitcid+".ipfs.w3s.link"+splitName,
         vote:voteCount
        }]
      })
    }

  }
  leading()
}
      },[])

let setVote = async(val)=>{
  let provider = new ethers.providers.Web3Provider(window.ethereum);
  let signer = provider.getSigner();
  let contractAddress = '0xf377894B9Fee15f56e5ad62E9e7e149255FE3581';
  let abi = ["function vote(address _party)"];
  let contract = new ethers.Contract(contractAddress,abi,signer);
  await contract.vote(val).then((res)=>{
    console.log(res);
  }).catch((err)=>{
    alert(err.code)
  })
}
    return(
        <>
            <div className='container-fluid'>

<div className='vote-container mt-5'>
  {/* container-1 */}
<div className="border p-2 mt-5">
<p className="fs-5 fw-lighter bharath">Live <span className="text-success">counts</span></p>
<table className='table margin-0 ps-5 pe-5'>
  <thead>
    <tr>
      <th>logo</th>
      <th>name</th>
      <th>address</th>
      <th>vote</th>
    </tr>
  </thead>
{
  registor.map((val)=>{
    return(
      <tr>
<td className="p-1"><img src={val.img} className="img-width" alt={val.name}/></td>
<td className="p-1 mt-2">{val.name}</td>
<td className="p-1 mt-2">{val.address.slice(0,10)}...</td>
<td className="p-1 mt-2 text-center">{val.vote+""}</td>
      </tr>
    )
  })
}
</table>
</div>
{/* container-2 */}
<div className="border mt-5 p-2">
<p className="fs-5 fw-lighter bharath">Vote <span className="text-success">poll</span></p>

<table className='table margin-0 ps-5 pe-5'>
  <thead>
    <tr>
      <th>logo</th>
      <th>name</th>
      <th>address</th>
      <th>vote</th>
    </tr>
  </thead>
{
  registor.map((val)=>{
    let add = val.address
    return(
      <tr>
<td className="p-1"><img src={val.img} className="img-width" alt={val.name}/></td>
<td className="p-1 mt-2">{val.name}</td>
<td className="p-1 mt-2">{val.address.slice(0,10)}...</td>
<button className="btn btn-success btn-sm ms-1 mt-1" onClick={()=>{setVote(add)}}>vote</button>
      </tr>
    )
  })
}
</table>
</div>
</div>

    </div>
        </>
    )
}

export default Home;