
import {  ethers } from 'ethers';
import { useEffect, useState } from 'react';
import { Link, Outlet } from 'react-router-dom';
import './App.css';

function App() {
  let [account,setAccount] = useState('');
  let [isConnected,connect] = useState(false);
  let basicSetup = (account)=>{
    connect(true)
    setAccount(account)
  }
  let connectWallet = async()=>{
if(!window.ethereum){
alert('no web3provider detected')
}else{
  let provider = new ethers.providers.Web3Provider(window.ethereum);
  let accounts = await provider.send('eth_requestAccounts',[]);
 window.localStorage.setItem('isConnected',true);
basicSetup(accounts[0])
}
  }
  useEffect(()=>{
let isConnect = window.localStorage.getItem('isConnected');
if(!isConnect){
  alert('connect wallet')

}else{
  connectWallet()
}
  },[])
  return (
    <div>
{/* navbar */}
<div className='container-fluid'>
<nav className="navbar navbar-expand-lg border-bottom">
<h1 className='bharath display-6 ms-2 d-inline fw-bold'>Bharath<span className='fw-lighter text-success display-6'>vote</span></h1>

  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" >
    <span className="navbar-toggler-icon"></span>
  </button>
  <div className="collapse navbar-collapse" id="navbarNav">
    <ul className="navbar-nav ms-auto p-1">
      <li className="nav-item active">
       <Link className='nav-link' to="/">Home</Link>
      </li>
      <li className="nav-item">
      <Link to="/register" className='nav-link'>Register now</Link>
      </li>
      <li className="nav-item">
     <Link to='/verifyme' className="nav-link">verify me</Link>
      </li>
      <li className="nav-item">
     <button className='btn-bg' onClick={connectWallet}>{isConnected ===true?<span>connected</span>:<span>Connect wallet</span>} </button>
      </li>
    </ul>
  </div>
</nav>
<div className='text-center bg-light p-1'>
 <span className='fs-6'> connected account: {account.slice(0,10)}...</span>
</div>
    </div>

    <Outlet />
</div>
  );
}

export default App;

