import { ethers } from "ethers";
import { useState } from "react"

function Verifyme(){
  let [add,setAdd] = useState("");
let approveMember = async(event) =>{
  event.preventDefault()
let provider = new ethers.providers.Web3Provider(window.ethereum);
let signer = provider.getSigner();
let contractAddress = "0x5Ef3dC125b56512F162e9d4745EDbd0bDe5FEa45";
let abi = ["function registerMember(address _member)"];
let contract = new ethers.Contract(contractAddress,abi,signer);
 await contract.registerMember(add).then((res)=>{
  console.log(res)
}).catch((err)=>{
  alert(err.code)
});

  }

  return(
    <>
      <h2 className="display-6 bharath ms-1">Approve user to vote</h2>
  <br />
    <div className="verifyme mt-5">

<form className="d-flex m-1">
<input type="text" name="address" className="form-input" placeholder="paste address here..." onChange={(event)=>{setAdd(event.target.value)}}/>
 <button className="bor-btn me-1" onClick={approveMember}>approve to vote</button>
</form>
    </div>
    </>
  )
}
export default Verifyme