import { useFormik} from "formik";
import * as yup from 'yup'
import {Web3Storage} from 'web3.storage'
import { useState } from "react";
import {toast, ToastContainer} from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css';
import {ethers} from'ethers'
let api =  'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJzdWIiOiJkaWQ6ZXRocjoweGUyMzZDMDA0ZDM1QmU3Q0FGRDM2MjZCMkUzYjIzYjY5NDQzQjM1NTYiLCJpc3MiOiJ3ZWIzLXN0b3JhZ2UiLCJpYXQiOjE2NjE0MjM1NzUxMzIsIm5hbWUiOiJzaW1wbGUifQ.hgRWpUAjUIesDtxa1PzrtFIvlI8Tr37iNXNKStpXDV0'
let client = new Web3Storage({token:api})
function Register(){
let [img,setImg] = useState("");
let [disable,setDisable] = useState(false)
let formik = useFormik({
initialValues :{
    name:"",
    address:""
},
validationSchema : yup.object({
    name: yup.string().min(3,"parties name should be more than three letters").strict().trim("no space allowed").required('this field is required'),
    address:yup.string().required('parties address is required')
}),
onSubmit: async(data)=>{
    if(img === "" || img === null || img === undefined){
      toast.error('upload image')
    }else{
        setDisable(true)
        let obj = {
            name: data.name,
            cid: img
        }
        let blob =  new Blob([JSON.stringify(obj)], { type: 'application/json' });
        let file = new File([blob],"party.json")
        console.log(file)
        let ipfs = await client.put([file])
        setDisable(false)
        let result = ipfs+"/party.json"
        let contractAddress = "0xf377894B9Fee15f56e5ad62E9e7e149255FE3581"
        setDisable(true)
let abi = ["function registerParty(address party,string  _ipfs)"]
let provider = new ethers.providers.Web3Provider(window.ethereum);
let signer = provider.getSigner()
let contract = new ethers.Contract(contractAddress,abi,signer)
await contract.registerParty(data.address,result).then((res)=>{
    console.log(res)
}).catch((err)=>{
   alert(err.code);
});
setDisable(false)
    }
}
})
let imageUploader = async(event)=>{

let file = event.target.files[0];
console.log(file)
let filename = file.name
setDisable(true)
let cid = await client.put([file],{
    name:filename
})
setDisable(false)
setImg(cid+"/"+filename);

}

    return(
        <>

  <div>
    <h6 className="bharath display-6 fw-lighter ms-2">Register your Party</h6>
<div className="register">
<div>
{disable ===  true? <div class="linear-activity">
    <div class="indeterminate"></div>
</div>: null}
<form onSubmit={formik.handleSubmit}>
<div className="form-group m-3">
<label className="form-label">parties name</label>
    <input type="text" name="name" placeholder="type your parties name..." className="form-control" value={formik.values.name} onChange={formik.handleChange}/>
    {formik.errors.name ?<p className="text-danger">{formik.errors.name} </p>: null}
</div>
<div className="form-group m-3">
<label className="form-label">parties address</label>
    <input type="text" name="address" placeholder="paste parties address..." className="form-control" value={formik.values.address} onChange={formik.handleChange}/>
    {formik.errors.address?<p className="text-danger">{formik.errors.address} </p>: null}
</div>
<div className="form-group m-3">
<label className="form-label">parties symbol</label>
    <input type="file" accept="image/png"className="form-control" disabled={disable} onChange={(event)=>{
imageUploader(event)
    }}/>

</div>
<button className="btn btn-success mt-3 btn-full" type="submit" disabled={disable}>Register now</button>


</form>
</div>
</div>

  </div>

  <ToastContainer />
     </>
    )
}
export default Register;

